.home {
  display: flex;

  .homeContainer {
    flex: 7;

    .widgets {
      width: 60%;
      display: flex;
      padding: 20px;
      gap: 30px;
    }

    .charts {
      display: flex;
      padding: 20px;
      gap: 50px;
    }

    .listContainer {
      padding: 20px;
      box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
      margin: 20px;
    }
  }
}

@media (max-width: 1600px) {
  .home {
    .homeContainer {
      flex: 6;

    .widgets {
            width: 80%;
        }
    }
  }
}
@media (max-width: 1200px) {
  .home {
    .homeContainer {
      flex: 5;

    .widgets {
            width: 90%;
            font-size: 30px;
        }
    }
  }
}
