.products {
    display: flex;
    width: 100%;

    .listContainer {
        margin-top: 50px;
        flex: 7;

        .cellWithStatus {
            padding: 5px;
            border-radius: 5px;

            &.active {
                background-color: greenyellow;
            }

            &.pending {
                background-color: red;
                color: white;
            }
        }
    }
}

@media (max-width: 1600px) {
    .products {
        .listContainer {
            flex: 6;

        }
    }
}

@media (max-width: 1200px) {
    .products {
        .listContainer {
            flex: 5;

        }
    }
}