.list {
    display: flex;
    width: 100%;

    .listContainer{
        flex: 7;

        .cellWithStatus{
        padding: 5px;
            border-radius: 5px;
        
            &.active {
                background-color: greenyellow;
            }

            &.pending {
                background-color: red;
                color: white;
            }
        }
    }
}

@media (max-width: 1600px) {
    .list {
        .listContainer {
            flex: 6;

        }
    }
}

@media (max-width: 1200px) {
    .list {
        .listContainer {
            flex: 5;

        }
    }
}