.single {
    display: flex;
    width: 100%;

    .singleContainer {
        flex: 7;

        .top {
            padding: 20px;
            display: flex;
            gap: 20px;

            .left {
                flex: 1;
                padding: 20px;
                box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
                display: flex;
                flex-direction: column;

                .topSection {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 10px;

                    .title {
                        font-size: 24px;
                        font-weight: bold;
                        color: rgb(145, 145, 145);
                    }

                    .editButton {
                        padding: 10px 20px;
                        border-radius: 4px;
                        background-color: #919191;
                        color: #fff;
                        border: none;
                        cursor: pointer;
                        transition: background-color 0.3s;

                        &:hover {
                            background-color: #575757;
                        }
                    }
                }

                .bottomSection {
                    display: flex;
                    margin-top: 30px;
                    justify-content: center;
                    align-items: center;

                    .itemImg {
                        width: 120px;
                        height: 120px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    .details {
                        display: flex;
                        flex-direction: column;

                        p {
                            margin-bottom: 10px;
                        }

                        .userName{
                            font-size: 35px;
                            font-size: bold;
                            color: #919191;
                        }
                    }
                }
            }

            .right {
                flex: 2;
            }
        }
    }


    .bottom{
        margin: 20px;

        .title{
            margin-bottom: 20px;
            font-size: 25px;
            font-weight: bold;
            color: #919191;
        }
    }
}


@media (max-width: 1600px) {
    .single {
        .singleContainer {
            flex: 6;

            .top {
                    .left {
                        font-size: 13px;
            
                        .topSection {
                            .title {
                                font-size: 20px;
                            }
            
                        }
                    }
                }
        }
    }
}

@media (max-width: 1200px) {
    .single {
        .singleContainer {
            flex: 4;

        }
    }
}