.featured {
    flex: 2;
    padding: 20px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .top-section {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;

        .total-revenue-heading {
            font-size: 18px;
        }

        .menu-icon {
            font-size: 24px;
        }
    }

    .bottom-section {
        display: flex;
        flex-direction: column;
        align-items: center;

        .progress-bar-section {
            margin-bottom: 20px;
        }

        .sales-today-section {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 10px;

            .sales-today-text {
                font-size: 14px;
            }

            .sales-today-money {
                font-size: 35px;
                font-weight: bold;
            }
        }

        .previous-transactions-section {
            margin-bottom: 10px;

            .previous-transactions-text {
                font-size: 12px;
                color: gray;
            }
        }

        .three-sections {
            display: flex;
            justify-content: space-between;
            width: 100%;

            .random-section {
                text-align: center;

                .random-heading {
                    font-size: 16px;
                }
            }

            .up-icon-section {
                display: flex;
                align-items: center;

                .up-icon {
                    font-size: 18px;
                    margin-right: 5px;
                }

                .price {
                    font-size: 18px;
                    font-weight: bold;
                }
            }
        }
    }


    
        .section-row {
            display: flex;
            justify-content: space-between;
            width: 80%;
            margin-top: 20px;
        }
    
        .section-item {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    
        .section-item h4 {
            margin: 0;
        }
    
        .item-price {
            display: flex;
            align-items: center;
        }
    
        .item-price p {
            margin: 0;
            margin-left: 5px;
            color: green;
        }

}