.userListContainer {
  .searchSection {
    padding: 10px 40px;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;

    .user-grid-search {
      margin-bottom: 10px;
      display: flex;


      input {
        width: 100%;
        padding: 5px;
        font-size: 16px;
        margin-right: 10px;
      }
    }

    .sort-value-selection {
      display: flex;
      align-items: center;
      margin-bottom: 10px;

      label {
        margin-right: 10px;
      }

      select {
        padding: 5px;
        font-size: 16px;
      }
    }
  }




  .user-grid {
    margin-bottom: 20px;

    .user-grid-header {
      display: flex;
      background-color: #f0f0f0;
      font-weight: bold;
      padding: 10px;

      .header-item {
        flex: 1;
        text-align: center;
      }
    }

    .user-grid-row {
      display: flex;
      border-bottom: 1px solid #ccc;
      padding: 10px;
      align-items: center;


      &:hover {
        background-color: #f8f8f8;
        cursor: pointer;
      }

      .grid-item {
        flex: 1;
        text-align: center;
        font-size: 15px;
      }

            .overflow-text {
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }

      .view-button {
        background-color: #007bff;
        color: #fff;
        border: none;
        padding: 5px 10px;
        cursor: pointer;
      }
    }
  }
}
