.sidebar {
    border-right: 1px solid rgb(228, 228, 228);
    flex: 1;
    height: 100vh;

    .top {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 180px;

        .logo{
            display: flex;
            flex-direction: column;
            padding: 0px 20px;
            font-size: 22px;
            font-weight: bold;

            p{
                margin-top: 5px;
                font-size: 14px;
                font-weight: 100;
            }
        }
    }

    .center {
        display: flex;
        align-content: center;
        margin-left: 30px;

        ul {
            list-style: none;
            padding: 0;
            margin: 0;

            .title {
                font-size: 12px;
                color: grey;
                margin-top: 20px;
            }

            li {
                display: flex;
                align-items: center;
                margin-bottom: 10px;
                margin-top: 5px;
                cursor: pointer;
                padding: 5px 0;

                .icon {
                    margin-right: 5px;
                }

                a {
                    display: flex;
                    align-items: center;
                    text-decoration: none;
                    color: black;

                    &:hover {
                        color: purple;
                        text-decoration: none;
                    }

                    &.active {
                        color: purple;
                        font-weight: bold;
                    }
                }

                span {
                    font-size: 15px;
                }
            }
        }
    }
}