.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 45px;
    border-bottom: 1px solid rgb(228, 228, 228);

    .search-section {
        display: flex;
        align-items: center;
        position: relative;

        input[type='text'] {
            width: 200px;
            padding: 5px;
            border: none;
            margin-right: 10px;
            height: 25px;
            padding-right: 25px;
            border: 1px solid rgb(228, 228, 228);;
        }

        .search-icon {
            font-size: 1.2rem;
            color: #555;
            position: absolute;
            right: 15px;
        }
    }

    .icons-section {
        display: flex;
        align-items: center;

        .icon {
            font-size: 1.5rem;
            color: #555;
            margin-right: 25px;
            cursor: pointer;
        }

        .languageIcon{
            font-size: 1.5rem;
            color: #555;
            cursor: pointer;
        }

        .language-section {
            display: flex;
            align-items: center;
            margin-right: 25px;

            .language-text {
                margin-left: 5px;
            }
        }

        .avatar {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }
    }
}

.notification-icon {
    position: relative;
    display: flex;
    margin-right: 10px;
}

.notification-circle {
    position: absolute;
    top: -5px;
    right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 18px;
    height: 18px;
    background-color: red;
    color: #fff;
    font-size: 12px;
    border-radius: 50%;
}

.notification-icon .icon {
    font-size: 1.5rem;
    color: #555;
    cursor: pointer;
}