.widget {
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    height: 100px;

    .left-section {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        width: 60%;

        .heading {
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 5px;
        }

        .user-count {
            font-size: 24px;
            font-weight: bold;
            margin-bottom: 5px;
        }

        .view-all-link {
            font-size: 14px;
            color: black;
            text-decoration: underline;
        }
    }

    .right-section {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-end;
        width: 40%;

        .up-icon {
            display: flex;
            align-items: center;
            color: green;

            .icon {
                font-size: 24px;
                margin-right: 5px;
            }

            .percentage {
                font-size: 16px;
            }
        }

        .icon {
            font-size: 24px;
        }

        .bgIcon{
            background-color: #ab48b7;
            color: white;
            border-radius: 20%;
            padding: 2px;
        }
    }
}


@media (max-width: 1200px) {
    .widget {
            .left-section {
                .heading {
                    font-size: 15px;
                }
    
                .user-count {
                    font-size: 20px;
                }
    
                .view-all-link {
                    font-size: 14px;
                }
            }
    
            .right-section {
                .up-icon {
                    .icon {
                        font-size: 24px;
                    }
    
                    .percentage {
                        font-size: 16px;
                    }
                }
    
                .icon {
                    font-size: 24px;
                }
            }
        }
}