.table{
    .cellWrapper{
        display: flex;
        align-items: center;

        .image{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            margin-right: 10px;
        }
    }

    .status{
        padding: 5px;
        border-radius: 5px;

        &.approved{
            background-color: greenyellow ;
        }

        &.pending{
            background-color: yellow;
        }
    }
}