.userListContainer {
  .user-grid {
    margin-bottom: 20px;

    .user-grid-header {
      display: flex;
      background-color: #f0f0f0;
      font-weight: bold;
      padding: 10px;

      div {
        flex: 1;
        text-align: center;
      }
    }

    .user-grid-row {
      display: flex;
      border-bottom: 1px solid #ccc;
      padding: 10px;
      align-items: center;

      &:hover {
        background-color: #f8f8f8;
        cursor: pointer;
      }

      &.pending {
        background-color: #ffc107;
      }

      .grid-item {
        flex: 1;
        text-align: center;
      }

      .view-button {
        background-color: #007bff;
        color: #fff;
        border: none;
        padding: 10px 15px;
        cursor: pointer;
      }

      .view-button:hover{
        background-color: #0068d8;
        transform: scale(1.05);
      }
          

      .approve-button {
        background-color: #28a745;
      }

      .reject-button {
        background-color: #dc3545;
      }
    }
  }

  .buttons-container {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    align-items: center;
    justify-content: space-between;
    padding: 0 50px;

    button {
      margin: 0 10px;
      padding: 10px 20px;
      border: none;
      cursor: pointer;
      font-weight: bold;
      text-transform: uppercase;
    }

    .buttons {
      color: white;
      background-color: black;
    }

    .active {
    }

    &.active-button {
      background-color: #007bff;
      box-shadow: 0 0 6px rgba(0, 123, 255, 0.5);
    }

    .pending-button {
      background-color: #ffc107;
    }

    .rejected-button {
      background-color: #dc3545;
    }

    .approved-button {
      background-color: #28a745;
    }
  }
}
